import {KeyValuePair} from '../../key-value-pair';
import {FilterInputTypeEnum} from '../filtering/filter-input-type.enum';
import {SortingDirectionEnum} from './sorting-direction.enum';

export class SortableColumn {
  name: string;
  title: string;
  direction: SortingDirectionEnum;
  bootstrapClass: string;
  filterInputType: FilterInputTypeEnum;
  values?: KeyValuePair[];
  sortable?: boolean;
  width?: string;

  constructor(name: string,
              title: string,
              direction: SortingDirectionEnum,
              bootstrapClass: string,
              filterInputType: FilterInputTypeEnum,
              values?: KeyValuePair[],
              sortable?: boolean,
              width?: string) {
    this.name = name;
    this.title = title;
    this.direction = direction;
    this.bootstrapClass = bootstrapClass;
    this.filterInputType = filterInputType;
    this.values = values;
    if (sortable === false) {
      this.sortable = false;
    } else {
      this.sortable = true;
    }
    this.width = width;
  }

  public toggleDirection() {
    if (this.direction === SortingDirectionEnum.DESC) {
      this.direction = null;
    } else if (this.direction === SortingDirectionEnum.ASC) {
      this.direction = SortingDirectionEnum.DESC;
    } else {
      this.direction = SortingDirectionEnum.ASC;
    }
  }

}
