import {Component, Input, OnInit} from '@angular/core';
import {NgbTooltip} from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'sepia-long-text-tooltip',
  templateUrl: './long-text-tooltip.component.html',
  styleUrls: ['./long-text-tooltip.component.scss']
})
export class LongTextTooltipComponent implements OnInit {

  @Input() longText: string;

  constructor() { }

  ngOnInit() {
  }

  openTooltip(divElement: HTMLDivElement, t: NgbTooltip) {
    if (divElement.offsetWidth < divElement.scrollWidth) {
      t.open();
    }
  }
}
