import {Component, EventEmitter, Input, Output} from '@angular/core';
import {NgbActiveModal} from '@ng-bootstrap/ng-bootstrap';
import {ModalExitEnum} from '../../model/modal-exit.enum';

@Component({
  selector: 'sepia-center-modal',
  templateUrl: './center-modal.component.html',
  styleUrls: ['./center-modal.component.scss']
})
export class CenterModalComponent {

  @Input() id: string;
  @Input() title: string;
  @Input() message: string;
  @Input() okButton: string;
  @Input() cancelButton: string;
  @Input() informationMessage: string;

  @Output() okClickEvent: EventEmitter<any> = new EventEmitter();

  resultOk = ModalExitEnum.OK;
  resultCancel = ModalExitEnum.CANCEL;

  constructor(public modal: NgbActiveModal) { }

  okWasClicked() {
    this.modal.close('Ok click');
    this.okClickEvent.emit();
  }

}
