import {Component, EventEmitter, forwardRef, Input, OnInit, Output} from '@angular/core';
import {
  ControlValueAccessor,
  FormControl,
  FormGroup,
  NG_VALIDATORS,
  NG_VALUE_ACCESSOR,
  Validator
} from '@angular/forms';
import {FilterInputTypeEnum} from '../../model/search/filtering/filter-input-type.enum';
import {SortableColumn} from '../../model/search/sorting/sortable-column';
import {SortingDirectionEnum} from '../../model/search/sorting/sorting-direction.enum';
import {SvgIcon} from '../svg/svg-icon.enum';

@Component({
  selector: 'sepia-table-filter-headers',
  templateUrl: './table-filter-headers.component.html',
  styleUrls: ['./table-filter-headers.component.scss'],
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => TableFilterHeadersComponent),
      multi: true
    },
    {
      provide: NG_VALIDATORS,
      useExisting: forwardRef(() => TableFilterHeadersComponent),
      multi: true,
    }
  ]
})
export class TableFilterHeadersComponent implements OnInit, ControlValueAccessor, Validator {

  @Input() sortableColumns: Array<SortableColumn>;
  @Input() actionsWidth = '10%';
  @Output() sortEvent: EventEmitter<SortableColumn> = new EventEmitter<SortableColumn>();
  @Output() searchEvent = new EventEmitter();
  @Output() clearEvent = new EventEmitter();
  inputText = FilterInputTypeEnum.INPUT_TEXT;
  dropdown = FilterInputTypeEnum.DROPDOWN;
  checkbox = FilterInputTypeEnum.CHECKBOX;
  radiobuttons = FilterInputTypeEnum.RADIO_BUTTONS;
  ascSortDirection = SortingDirectionEnum.ASC;
  form: FormGroup;
  onChange: any = (_: any) => {
  };
  onTouched: any = () => {
  };

  get value() {
    return this.form.value;
  }

  set value(value) {
    this.form.setValue(value);
    try {
      this.onChange(value);
    } catch (e) {
      /** Do nothing with the error */
    }
  }

  constructor() {
  }

  ngOnInit() {
    const formControls = {};
    this.sortableColumns.forEach(column => {
      formControls[column.name] = new FormControl('');
    });
    this.form = new FormGroup(formControls);


    this.form.valueChanges.subscribe(value => {
      try {
        this.onChange(value);
      } catch (e) {
        /** Do nothing with the error */
      }
    });
  }

  sort(column: SortableColumn): void {

    if (column.sortable) {
      column.toggleDirection();
      this.sortEvent.emit(column);
    }
  }

  clear() {
    this.form.reset();
    this.clearEvent.emit();
    this.searchEvent.emit();
  }

  search() {
    this.searchEvent.emit();
  }

  registerOnValidatorChange(fn: () => void): void {
  }

  writeValue(value: any): void {
    if (value) {
      this.value = value;
    }

    if (value === null) {
      this.form.reset();
    }
  }

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  validate(_: FormControl) {
    return this.form.valid ? null : {filters: {valid: false}};
  }

  get SvgIcon() {
    return SvgIcon;
  }
}
