import {Observable, throwError} from 'rxjs';
import {catchError} from 'rxjs/operators';
import {ApiError} from '../../model/api-error';
import {BusinessRuleService} from '../service/business-rule-service';

export class BusinessRuleComponent {
  withErrors = false;
  errorKeys: string[];

  constructor(private service: BusinessRuleService) {
    this.init();
  }

  init() {
    this.withErrors = false;
    this.errorKeys = [];
  }

  create(entity: any): Observable<any> {
    this.init();
    return this.service.create(entity).pipe(catchError(error => this.handleError(error.error)));
  }

  update(entity: any): Observable<any> {
    this.init();
    return this.service.update(entity).pipe(catchError(error => this.handleError(error.error)));
  }

  delete(id: any): Observable<any> {
    this.init();
    return this.service.delete(id).pipe(catchError(error => this.handleError(error.error)));
  }

  handleError(apiError: ApiError) {
    this.withErrors = true;
    if (apiError && apiError.errorKeys) {
      apiError.errorKeys.forEach(error => this.errorKeys.push(error));
    }
    return throwError(apiError);
  }
}
